@media (max-width: 767px) {
    .vision {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;

        align-items: center;
        h2 {
            font-family: "Cormorant-Regular";
            color: #1c1c1c;
            font-size: 1.75em;
            width: 95%;
        }
        .bloc-text {
            display: flex;
            order: 2;
            flex-direction: column;
            align-items: center;
            p {
                font-family: "Inter-Regular";
                text-align: center;
                width: 95%;
            }
        }
        .picture {
            margin: 10% 0;
            display: flex;
            order: 1;
            width: 100%;
            img {
                width: 100%;
                object-fit: cover;
            }
        }
        .call {
            display: flex;
            order: 3;
            flex-direction: column;
            align-items: center;
            padding: 10% 0;
        }
    }
}

@media (min-width: 768px) and (max-width: 1023px) {
    .vision {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;

        justify-content: space-evenly;
        h2 {
            width: 100%;
            text-align: center;
        }
        .bloc-text {
            text-align: center;
            width: 90%;
            p {
                font-size: 1.4em;
                margin-top: 5px;
            }
        }
        .picture {
            width: 100%;
        }
        .call {
            display: flex;
            flex-direction: column;
            padding: 5% 0;
            width: 100%;
            text-align: center;
            align-items: center;
            p {
                font-family: "Cormorant-Regular";
                color: #c88a16;
                font-weight: 600;
                font-size: 1.8em;
                text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.15);
            }
        }
    }
}
@media (min-width: 1024px) {
    .vision {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        height: 100vh;
        align-content: flex-start;
        h2 {
            font-family: "Cormorant-Regular";
            color: #1c1c1c;
            font-weight: 800;
            margin: 2% 0 0 8%;
            font-size: 2.5em;
        }
        .bloc-text {
            margin-top: 5%;
            width: 40%;
            color: #1c1c1c;
            font-family: "Inter-Regular";
            font-size: 1.5em;
        }
        .picture {
            width: 30%;
            border-radius: 10px;
            height: 40%;
            img {
                border-radius: 10px;
                height: 100%;
            }
        }
        .call {
            position: relative;
            width: 80%;
            text-align: center;
            color: #c88a16;
            font-size: 2em;
            font-family: "Cormorant-Regular";
            bottom: 30%;
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            button {
                margin-top: 1.5rem;
            }
        }
    }
}
