@media (max-width: 767px) {
    .projects-caroussell {
        background: #faf9f6;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 2rem 1rem;

        .caroussel-container {
            display: grid;
            grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
            gap: 2rem;
            width: 100%;
            max-width: 1200px;
        }

        .project-slide {
            background: #fff;
            border-radius: 12px;
            overflow: hidden;
            box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
            transition: transform 0.2s ease;
            cursor: pointer;

            .project-image {
                width: 100%;
                height: 200px;
                object-fit: cover;
            }

            .information {
                padding: 1rem;
                text-align: center;

                h3 {
                    font-family: "Cormorant-Regular";
                    color: #c88a16;
                    font-size: 1.5rem;
                }

                p {
                    font-family: "Inter-Regular";
                    font-size: 1rem;
                    color: #1c1c1c;
                }

                .meta {
                    color: #999;
                    font-size: 0.875rem;
                }
            }
        }
    }

    // ---------- MODALE ( mobile) ----------

    .modal-overlay {
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        backdrop-filter: blur(8px);
        -webkit-backdrop-filter: blur(8px);
        background-color: rgba(0, 0, 0, 0.4);
        z-index: 9999;
        display: flex;
        justify-content: center;
        align-items: center;

        overflow-y: auto;
    }

    .modal-content {
        background: #ffffff;
        border-radius: 12px;
        max-width: 800px;
        width: 90vw;
        max-height: 90vh;
        display: flex;
        flex-direction: column;
        overflow: hidden;
    }

    .modal-gallery-scroll {
        height: 50%;
        display: flex;
        flex-direction: row;
        overflow-x: auto;
        scroll-snap-type: x mandatory;
        -webkit-overflow-scrolling: touch;

        &::-webkit-scrollbar {
            display: none;
        }

        .modal-main-image {
            width: 100%;
            height: 100%;
            object-fit: cover;
            flex-shrink: 0;
            scroll-snap-align: start;
        }
        .scroll-arrow {
            opacity: 0.55;
            color: white;
            position: fixed;
            top: 30% !important;
        }
        .left {
            left: 7%;
        }
        .right {
            right: 7%;
        }
    }

    .modal-info {
        overflow-y: auto;
        height: 50%;
        padding: 1rem;
        background-color: #fff;

        h2 {
            font-family: "Cormorant-Regular";
            font-size: 1.3rem;
            color: #c88a16;
            margin-bottom: 0.5rem;
        }

        h4 {
            font-size: 1rem;
            color: #666;
            margin-bottom: 0.75rem;
        }

        .meta {
            font-size: 0.85rem;
            color: #999;
            margin-bottom: 0.5rem;
        }

        p {
            font-size: 0.9rem;
            line-height: 1.4;
            margin-bottom: 0.5rem;
        }

        .modal-close {
            margin-top: 1rem;
            align-self: center;
            padding: 0.4rem 1.2rem;
            font-size: 0.9rem;
            border-radius: 6px;
            border: 1px solid #aaa;
            background: #c88a16;
            color: #ffff;
            cursor: pointer;
        }
    }

    .projects-caroussell {
        flex-direction: column;
        // height: 90vh;
        .caroussel-container {
            display: flex;
            overflow-x: auto;
            scroll-snap-type: x mandatory;
            -webkit-overflow-scrolling: touch;
            gap: 1.5rem;
            padding: 0 1rem;
            scroll-padding-left: 1rem;

            &::-webkit-scrollbar {
                display: none;
            }
        }

        .project-slide {
            flex: 0 0 90%;
            scroll-snap-align: center;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            .project-image {
                width: 100%;
                height: 50%;
                object-fit: cover;
                border-radius: 10px 10px 0 0;
            }

            .information {
                width: 80%;
                height: 50%;
                p {
                    font-size: 1em;
                    text-align: center;
                }

                h3 {
                    font-size: 1.25em;
                    color: #c88a16;
                }

                .meta {
                    color: #a9a9a9;
                    font-size: 0.875em;
                    margin: 0 1em 1em;
                }
            }
        }
    }

    .modal-content {
        width: 90vw;
        height: 100vh;
        border-radius: 12px;
        display: flex;
        flex-direction: column;
    }

    .modal-main-image {
        height: 240px;
    }
}

