            width: 100%;
            height: 100vh;
            object-fit: cover;
            scroll-snap-align: center;
            flex: 0 0 100%;
        }
    }

    .scroll-arrow {
        opacity: 0.55;
        color: #ffff;
        // display: none; // âŒ on cache les chevrons sur desktop
        rotate: (90deg);
        position: fixed;
        left: 27%;
