@media (max-width: 767px) {
    .politicy {
        width: 100%;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        display: flex;
        height: 200vh;
        div {
            width: 95%;
            text-align: center;
            font-family: "Inter-Regular";
            color: #1c1c1c;
            p {
                margin: 2% 0 0 0;
                font-size: 0.8em;
            }
        }
    }
}
