footer {
    background-color: #fdfbf8;
    font-family: "Cormorant Garamond", serif;
    color: #333;
    text-align: center;
    padding: 3em 1.5em 2em;

    h3 {
        font-size: 1.125em;
        font-weight: 500;
        letter-spacing: 0.08em;
        color: #000;
        opacity: 0.9;
        transition: color 0.3s ease;
    }

    .sociaux {
        display: flex;
        justify-content: center;
        gap: 1.5em;
        margin-bottom: 1.5em;

        svg {
            width: 24px;
            height: 24px;
            filter: grayscale(100%);
            opacity: 0.8;
            transition:
                filter 0.3s ease,
                transform 0.3s ease,
                opacity 0.3s;

            &:hover {
                filter: none;
                transform: scale(1.1);
                opacity: 1;
            }
        }
    }

    .legales {
        display: flex;
        flex-direction: column;
        gap: 0.5em;
        font-size: 0.875em;
        margin-bottom: 2em;
        align-items: center !important;

        a {
            color: #333;
            text-decoration: none;
            transition: color 0.3s ease;

            &:hover {
                color: #c88a16;
            }
        }
    }

    /* === TABLETTE (768â€“1023px) === */
    @media (min-width: 768px) and (max-width: 1023px) {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        align-items: center;
        text-align: left;

        .legales,
        .sociaux {
            width: 50%;
            margin-bottom: 0;

            svg {
                width: 44px;
                height: 44px;
            }
        }

        h3 {
            width: 100%;
            text-align: center;
        }
    }

    /* === DESKTOP (1024px+) === */
    @media (min-width: 1024px) {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 2rem;
        padding: 2% 5%;

        .legales {
            flex-direction: column;
            align-items: center;
            font-size: 1.275em;
            flex: 1;
            order: 2;
        }

        h3 {
            font-size: 1.425em;
            order: 1;
        }

        .sociaux {
            order: 3;
            flex: 0 0 auto;
            gap: 1rem;

            svg {
                width: 54px;
                height: 54px;

                &:hover {
                    filter: grayscale(100%);
                    transform: scale(1.1);
                    opacity: 1;
                    color: #c88a16;
                }
            }
        }
    }
}
