@media (max-width: 767px) {
    .legals {
        width: 100%;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        display: flex;
        height: 200vh;
        h2 {
            text-align: center;
        }
        div {
            width: 95%;
            text-align: center;
            font-family: "Inter-Regular";
            color: #1c1c1c;
            p {
                margin: 2% 0 0 0;
                font-size: 0.8em;
            }
        }
    }
}
@media (min-width: 768px) {
    .legals {
        display: flex;
        width: 100%;
        flex-direction: column;
        justify-content: space-around;
        height: 150vh;
        h2 {
            margin: 5% 0 0 2%;
        }
        div {
            width: 95%;
            text-align: center;
            font-family: "Inter-Regular";
            color: #1c1c1c;
            p {
                margin: 2% 0 0 0;
                font-size: 0.8em;
            }
        }
    }
}
