button {
    cursor: pointer;
    background: #c88a16;
    border: none;
    border-radius: 5px;
    height: 3em;
    width: 11em;

    display: flex;
    align-items: center;
    justify-content: center;

    text-align: center;
    color: #fff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);

    transition:
        transform 0.3s ease,
        background 0.3s ease;

    &:hover {
        transform: scale(1.05);
        background: #f7c465;
    }

    a {
        color: #fff;
        font-family: "Cormorant-Regular";
        font-weight: 500;
        font-size: 1.1em;
        text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
        transition: font-weight 0.3s ease;

        &:hover {
            font-weight: 700;
        }
    }
}
