@media (max-width: 1023px) {
    .projects {
        background: #ffff;
        h2 {
            margin-top: 25px;
            text-align: center;
            font-family: "Cormorant-Regular";
            font-size: 1.75em;
            font-weight: 600;
            margin-bottom: 1em;
            letter-spacing: 0.08em;
        }
        .intoduction {
            font-family: "Inter-Regular";
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
            height: 50vh;
            align-items: center;
            width: 100%;
            span,
            p {
                font-size: 1.125em;
                font-weight: 400;
                line-height: 1.6;
                text-align: center;
                width: 80%;
                .hook {
                    color: #c88a16;
                }
            }
        }
        .call {
            display: flex;
            flex-direction: column;
            align-items: center;
            padding: 5% 3%;
            justify-content: space-evenly;
            p {
                width: 80%;
                text-align: center;
            }
        }
    }
}
@media (min-width: 1024px) {
    .projects {
        display: flex;
        flex-wrap: wrap;
        h2 {
            margin-top: 5%;
            margin-left: 5%;
            width: 100%;
            color: #1c1c1c;
            font-size: 3.5em;
            letter-spacing: 0.08em;
        }
        .intoduction {
            opacity: 0;
            transform: translateY(20px);
            animation: fadeUp2 1s ease-out forwards;
            display: flex;
            flex-direction: column;
            justify-content: center;
            width: 100%;
            text-align: center;
            font-family: "Inter-Regular";
            font-size: 1.5em;
            padding: 3% 0;
            align-items: center;
            .catchphrase {
                width: 70%;
                color: #1c1c1c;
                font-weight: 700;
                margin-bottom: 25px;
            }
            p {
                width: 35%;
                margin: 0;
                .hook {
                    color: #c88a16;
                }
            }
        }
        .projects-caroussell {
            height: 130vh;

            .caroussel-container {
                flex-wrap: wrap;
                height: 100%;
            }
        }
        .call {
            display: flex;
            width: 100%;
            justify-content: center;
            align-items: center;
            height: 30vh;
            p {
                font-family: "Inter-Regular";
                text-align: center;
                font-size: 1.5em;
                margin: 0;
            }
            button {
                position: absolute;
                left: 75%;
            }
        }
    }
}
