@media (max-width: 767px) {
    @keyframes slideIn {
        from {
            transform: translateX(-100%);
        }
        to {
            transform: translateX(0);
        }
    }
    .mobile-drawer {
        // height: 35vh;
        display: flex;
        flex-direction: column;
        width: 45%;
        background-color: rgba(250, 249, 246, 0.95); // fond plus opaque
        backdrop-filter: blur(12px);
        -webkit-backdrop-filter: blur(12px); // pour Safari
        box-shadow: 2px 0 12px rgba(0, 0, 0, 0.08); // lÃ©gÃ¨re ombre Ã  droite
        position: absolute;
        opacity: 0.9;
        border-radius: 0 0 10px 0;
        transform: translateX(0%);
        z-index: 1000;
        transform: translateX(-100%);
        animation: slideIn 0.3s forwards;
        &.open {
            transform: translateX(50%);
        }

        nav {
            display: flex;
            flex-direction: column;
            width: 100%;
            height: 30vh;
            ul {
                display: flex;
                flex-direction: column;
                width: 100%;
                height: 100%;
                justify-content: space-evenly;
            }
        }
        a {
            display: flex;
            width: 100%;
            color: black;
            justify-content: center;
            font-weight: bolder;
            li {
                display: flex;
                text-align: center;
            }
        }
        .close-button {
            color: #1c1c1c;
            background: transparent;
            border: none;
            width: 30%;

            display: flex;
            align-self: flex-end;
        }
    }
    .active {
        color: #c88a16 !important;
    }
}
