header {
    display: flex;
    align-items: center;
    background: #faf9f6;
    padding: 3% 2%;
    font-family: "Cormorant-Regular";
    color: #1c1c1c;

    h1 {
        font-size: clamp(2rem, 5vw, 3.5rem);
        letter-spacing: 1.5px;
    }

    .navigation {
        display: none;
    }

    .menu-burger {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        height: 30px;

        span {
            width: 20px;
            height: 3px;
            background: #1c1c1c;
            border-radius: 20px;
        }
    }

    // âœ… TABLETTE (>=768px)
    @media (min-width: 768px) {
        justify-content: space-around;
        padding: 1.5% 2%;
        .menu-burger {
            display: none;
        }

        .navigation {
            display: flex;
            width: 60%;
            align-items: center;

            ul {
                display: flex;
                justify-content: space-around;
                width: 100%;

                li {
                    font-family: "Inter-Regular";
                    font-size: clamp(1rem, 2.2vw, 1.4rem);
                }
            }
        }
    }

    // âœ… DESKTOP (>=1024px)
    @media (min-width: 1024px) {
        padding-top: 1rem;
        padding-bottom: 1rem;

        a {
            font-size: 1.5em;
            transition: color 0.3s ease;

            &:hover {
                color: #c88a16;
            }
        }
    }

    // âœ… MOBILE (<=767px)
    @media (max-width: 767px) {
        justify-content: space-between;
        //     height: 7vh;
    }
}
