//mobile
@media (max-width: 767px) {
    .hero {
        // background: url("../../assets/images/background-hero.png");
        // background-size: cover;
        height: 93.1vh;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: space-between;

        h2 {
            margin-top: 20%;
            color: #faf9f6;
            font-family: "Cormorant-Regular";
            text-shadow: 0 2px 4px rgba(0, 0, 0, 0.2);
            width: 80%;
            font-size: clamp(1.5em, 5vw, 2.25em); /* entre 24px et 36px */
            font-weight: 400;
            line-height: 1.5;
            opacity: 0;
            animation: fadeUp 1s ease-out forwards;
            animation-delay: 0.5s;
        }
        .bottom {
            display: flex;
            justify-content: flex-end;
            width: 95%;
            .arrow-down {
                height: 6em;
                width: 3em;
                animation: bounce 2s infinite;
            }
            svg {
                color: #ffff;
            }
        }
    }
}
.manifest {
    text-align: center; /* ou left si tu prÃ©fÃ¨res une lecture Ã©ditoriale plus posÃ©e */

    font-family: "Inter-Regular";
    font-size: 1.125em; /* â‰ˆ 18px */
    line-height: 1.75;
    font-weight: 400;
    color: #222;

    padding: 5% 4%;
    text-shadow: 0 2px 4px rgba(0, 0, 0, 0.15);
}

.manifest p {
    margin: 5% 0;
}

.manifest .accent {
    color: #a9a9a9; /* Bleu-gris un peu plus dense */
    font-style: italic;
    font-weight: 500;
    display: inline-block;
    margin-top: 0.5em;
    transition: all 0.6s ease;
    opacity: 0;
    transform: translateY(10px);
    animation: fadeInUp 1s ease forwards;
    animation-delay: 0.3s;
}

@keyframes fadeInUp {
    to {
        opacity: 1;
        transform: translateY(0);
    }
}
//tablette
@media (min-width: 768px) and (max-width: 1023px) {
    .hero {
        height: 92.6vh;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;
        h2 {
            font-size: 2.5em;
            color: #faf9f6;
            margin: 10% 0 0 10%;
            width: 60%;
            text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3); /* Ajout recommandÃ© */
        }
        .bottom {
            width: 95%;
            text-align: right;
            color: #faf9f6;
            .arrow-down {
                height: 6em;
                width: 3em;
                animation: bounce 2s infinite;
            }
        }
    }
    .manifest {
        padding: 2% 4%;
    }
}

@media (min-width: 1024px) {
    .hero {
        width: 100%;
        height: 92vh;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        h2 {
            color: #faf9f6;
            font-family: "Cormorant-Regular";
            font-size: clamp(2em, 4.5vw, 4em);
            width: 60%;
            margin: 7% 0% 0 5%;
            text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3); /* Ajout recommandÃ© */
            animation: fadeUp 1s ease-out forwards;
            animation-delay: 0.5s;
        }
        .bottom {
            width: 95%;
            text-align: right;
            color: #faf9f6;
            .arrow-down {
                height: 6em;
                width: 4em;
                animation: bounce 2s infinite;
            }
        }
    }
    .manifest {
        margin: 0;
        padding: 0;
        width: 100%;
        max-width: none;
        text-align: center;
        justify-self: center;
        background: #faf9f6;
        p {
            padding: 2% 10%;
            margin: 0;
            font-size: clamp(1.6rem, 3vw, 2rem); // â‰ˆ 19pxâ€“25px
            text-align: center;
        }
    }
}
