// general

.form {
    display: flex;
}
@media (max-width: 767px) {
    .form {
        flex-direction: column;
        background: #faf9f6;
        width: 100%;
        margin: 0 auto;
        border-radius: 15px;
        align-items: center;
        justify-content: space-around;
        padding: 10% 5%;
        gap: 40px;

        div {
            display: flex;
            flex-direction: column;
            font-family: "Inter-Regular", sans-serif;
            align-items: center;
            justify-content: space-around;
            width: 100%;

            label {
                padding: 1.5% 0;
                display: flex;
                align-content: center;
                justify-content: center;
                align-items: center;
                background: #5a5a5a;
                width: 35%;
                border-radius: 5px 5px 0 0;
                text-align: center;
                color: #faf9f6;
                opacity: 0.5;

                font-size: 0.95em;
            }

            input,
            textarea {
                width: 90%;
                // border: none;
                // background: beige;
                border-radius: 0 5px 5px 5px;
                // border: 0 2px 2px 2px solid#e0ddd7;
                // border: 2px solid #e0ddd7;
                border-left: 2px solid #e0ddd7;
                border-right: 2px solid #e0ddd7;
                border-bottom: 2px solid #e0ddd7;
                border-top: none;
                // border-radius: 10px;
                background: #fdfbf8;
                padding: 3% 0;
                box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
                transition:
                    border 0.3s ease,
                    box-shadow 0.3s ease;

                &::placeholder {
                    color: #b4b4b4;
                    font-size: 0.9em;
                    outline: none;
                }

                &:focus {
                    border: 2px solid #c88a16;
                    outline: none;
                    box-shadow: 0 0 6px rgba(200, 138, 22, 0.3);
                    text-align: center;
                    font-family: "Inter-Regular", sans-serif;
                }
            }

            input {
                padding: 6% 0;
            }

            textarea {
                height: 15em;
                resize: vertical;
                margin-bottom: 10px;
            }
        }

        button {
            background: #c88a16;
            color: white;
            padding: 12px 24px;
            border-radius: 12px;
            border: none;
            font-weight: 600;
            font-family: "Cormorant-Regular", sans-serif;
            font-size: 1rem;
            box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
            cursor: pointer;
            margin-top: 10px;
            transition: background 0.3s ease;

            &:hover {
                background: #af7412;
                transform: translateY(-2px);
            }
        }
    }
}

@media (min-width: 768px) and (max-width: 1023px) {
    .form {
        order: 5;
        width: 100%;

        flex-wrap: wrap;
        align-content: space-around;
        margin-left: 8%;
        transform: translateY(-20%);
        align-items: center;
        .phone {
            width: 35%;
        }

        div {
            margin: 5% 0;
            display: flex;
            flex-direction: column;
            width: 30%;
            label {
                background: #5a5a5a;
                width: 35%;
                border-radius: 5px 5px 0 0;
                text-align: center;
                color: #faf9f6;
                opacity: 0.5;
            }
            input,
            textarea {
                width: 65%;
                // border: none;
                // background: beige;
                border-radius: 0 5px 5px 5px;
                // border: 0 2px 2px 2px solid#e0ddd7;
                // border: 2px solid #e0ddd7;
                border-left: 2px solid #e0ddd7;
                border-right: 2px solid #e0ddd7;
                border-bottom: 2px solid #e0ddd7;
                border-top: none;
                // border-radius: 10px;
                background: #fdfbf8;
                height: 2.5em;
                box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
                transition:
                    border 0.3s ease,
                    box-shadow 0.3s ease;

                &::placeholder {
                    color: #b4b4b4;
                    font-size: 0.9em;
                    outline: none;
                }

                &:focus {
                    border: 2px solid #c88a16;
                    outline: none;
                    box-shadow: 0 0 6px rgba(200, 138, 22, 0.3);
                    text-align: center;
                    font-family: "Inter-Regular", sans-serif;
                }
            }
            textarea {
                height: 10em;
            }
        }
        .message {
            width: 60%;
        }
        .email {
            width: 55%;
        }
    }
}
@media (min-width: 1024px) {
    .form {
        height: 50vh;
        width: 70%;

        flex-wrap: wrap;
        justify-content: space-between;
        align-items: flex-end;
        margin-bottom: 5%;
        div {
            display: flex;
            flex-direction: column;
            label {
                background: #5a5a5a;
                padding: 1.5% 0;
                border-radius: 5px 5px 0 0;
                text-align: center;
                color: #faf9f6;
                opacity: 0.5;
                text-align: center;
            }
            input,
            textarea {
                border-radius: 0 5px 5px 5px;
                // border: 0 2px 2px 2px solid#e0ddd7;
                // border: 2px solid #e0ddd7;
                border-left: 2px solid #e0ddd7;
                border-right: 2px solid #e0ddd7;
                border-bottom: 2px solid #e0ddd7;
                border-top: none;
                // border-radius: 10px;
                background: #fdfbf8;

                box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
                transition:
                    border 0.3s ease,
                    box-shadow 0.3s ease;

                &::placeholder {
                    color: #b4b4b4;
                    font-size: 0.9em;
                    outline: none;
                }

                &:focus {
                    border: 2px solid #c88a16;
                    outline: none;
                    box-shadow: 0 0 6px rgba(200, 138, 22, 0.3);
                    text-align: center;
                    font-family: "Inter-Regular", sans-serif;
                }
                padding: 12px 0;
                text-align: center;
            }
        }
        .last-name,
        .first-name,
        .phone {
            width: 30%;
            label {
                width: 30%;
            }
            input {
                width: 50%;
            }
        }
        .email {
            width: 35%;
            label {
                width: 25%;
            }
            input {
                width: 41%;
            }
        }
        .subject {
            width: 65%;
            label {
                width: 20%;
            }
            input {
                width: 40%;
            }
        }
        .message {
            width: 70%;
            label {
                width: 20%;
            }
            textarea {
                padding: 5px;
                width: 45%;
                height: 10em;
            }
        }
        button {
            position: relative;
            right: 45%;
            bottom: 10%;
        }
    }
}
