@media (max-width: 767px) {
    .about {
        background: #ffff;
        h2 {
            font-size: 2.5em;
            margin-top: 25px;
            text-align: center;
            font-weight: 600;
            font-family: "Cormorant-Regular";
        }
        .introduction {
            display: flex;
            flex-direction: column;

            margin-top: 25px;
            align-items: center;
            justify-content: space-evenly;
            img {
                width: 100%;
                height: 35vh;
                object-fit: cover;
                order: 1;
            }
            p {
                font-family: "Inter-Regular";
                width: 80%;
                order: 3;
                margin: 8% 0;
                width: 80%;
                text-align: center;
            }
            .catch {
                margin-top: 10%;
                font-weight: bold;
                width: 100%;
                color: #c88a16;
                font-family: "Cormorant-Regular";
                order: 2;
                text-align: center;
                font-size: 1.4em;
                text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.15);
            }
        }
    }
}
@media (min-width: 768px) and (max-width: 1023px) {
    .about {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;

        h2 {
            margin: 5% 0;
            font-weight: 600;
            font-family: "Cormorant-Regular";
            font-size: 2em;
        }
        .introduction {
            display: flex;
            flex-direction: column-reverse;
            align-items: center;
            justify-content: space-around;
            img {
                width: 70%;
                border-radius: 10px;
                margin: auto;
            }
            p {
                margin: 0;
                width: 90%;
                font-family: "Inter-Regular";
            }
            .catch {
                letter-spacing: 0.05em;
                font-weight: bold;
                width: 100%;
                margin: 7% 0;
                color: #c88a16;
                font-family: "Cormorant-Regular";
                font-size: 1.75em;
                text-align: center;
                text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.15);
            }
        }
    }
}
@media (min-width: 1024px) {
    .about {
        display: flex;
        flex-wrap: wrap;
        h2 {
            margin-left: 7%;
            margin-top: 5%;
            width: 90%;
            font-size: 3.5em;
            color: #1c1c1c;
            font-family: "Cormorant-Regular";
            font-weight: 800;
        }
        .introduction {
            gap: 4rem; // ðŸ‘ˆ AJOUTE CECI ICI âœ…
            display: flex;
            justify-content: space-around;
            align-items: center;
            height: 60vh;
            img {
                width: 25%;
                height: 60%;
                border-radius: 10px;
            }
            p {
                line-height: 1.7;
                font-size: 1.7em;
                width: 45%;
                display: flex;
                flex-direction: column;
                height: 80%;
                justify-content: flex-start;
                .catch {
                    margin-top: 10%;
                    color: #c88a16;
                    font-family: "Cormorant-Regular";
                    font-weight: 800;
                    font-size: 1.9em;
                }
            }
        }
    }
}
