@media (max-width: 1023px) {
    .organisation {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        padding: 2rem 0;
        div {
            text-align: center;
            width: 80%;
            h3 {
                color: #c88a16;
                font-family: "Inter-Regular";
                font-size: 1.25em;
                font-weight: 600;
            }
            p {
                margin-top: 5px;
                font-family: "Inter-Regular";
                font-size: 1.125em;
                line-height: 1.7;
            }
        }
    }
}

@media (min-width: 1024px) {
    .organisation {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        gap: 2rem;
        padding: 4rem 2rem;

        text-align: center;

        // Introduction (1Ã¨re div)
        > div:nth-child(1) {
            width: 100%;
            p {
                font-size: 1.8em;
                color: #444;
                width: 40%;
                font-family: "Inter-Regular";
            }
        }

        // Conteneur des 3 principes (div 2, 3, 4)
        > div:nth-child(2),
        > div:nth-child(3),
        > div:nth-child(4) {
            width: 28%;
            text-align: center;
            h3 {
                font-size: 2.25rem;
                color: #c88a16;
                margin-bottom: 0.5rem;
            }

            p {
                font-size: 1.5em;
                color: #333;
                line-height: 1.6;
            }
        }

        // Grille horizontale pour les 3 cartes
        > div:nth-child(2),
        > div:nth-child(3),
        > div:nth-child(4) {
            display: inline-block;
            vertical-align: top;
            min-height: 240px; // selon ton design
        }

        // Conteneur global : transformation en ligne
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;

        // Conclusion (5e div)
        > div:nth-child(5) {
            max-width: 800px;

            p {
                font-style: italic;
                color: #555;
                font-size: 1.4em;
            }
        }

        // CallToAction (6e div)
        button {
            margin-top: 1.5rem; // au lieu de 3rem
        }
    }
}
