@media (max-width: 1023px) {
    .principles {
        position: relative; // nÃ©cessaire pour positionner la flÃ¨che
        padding: 2rem 1rem;
        background: #faf9f6;

        h2 {
            text-align: center;
            font-size: 2em;
            font-weight: 600;
            margin-bottom: 1.5rem;
            font-family: "Cormorant-Regular";
            color: #1c1c1c;
        }

        .carousel-row {
            display: flex;
            overflow-x: auto;
            scroll-snap-type: x mandatory;
            scroll-behavior: smooth;
            gap: 1rem;
            padding-bottom: 1rem;

            // cacher la scrollbar
            scrollbar-width: none; // Firefox
            -ms-overflow-style: none; // IE 10+
            &::-webkit-scrollbar {
                display: none; // Chrome, Safari
            }

            .bloc-twist {
                display: contents; // permet de fusionner les blocs internes dans le flux horizontal
            }

            .bloc {
                flex: 0 0 85%;
                scroll-snap-align: center;
                background: white;
                border-radius: 12px;
                padding: 1.5rem;
                box-shadow: 0 4px 10px rgba(0, 0, 0, 0.05);
                min-width: 80%;

                display: flex;
                flex-direction: column;
                align-items: center;
                text-align: center;

                h3 {
                    font-size: 1.5em;
                    font-family: "Cormorant-Regular";
                    color: #946b2d;
                    margin-bottom: 1rem;
                    font-weight: 700;
                }

                p {
                    font-family: "Inter-Regular";
                    font-style: italic;
                    font-size: 1.2em;
                    color: #333;
                }
            }
        }

        .scroll-arrow {
            position: absolute;
            right: 1rem;
            top: 60%;
            transform: translateY(-50%);
            font-size: 1.5rem;
            color: #d3c5b2;
            opacity: 0.6;
            animation: bounceArrow 1.2s infinite;
            pointer-events: none;
        }

        @keyframes bounceArrow {
            0%,
            100% {
                transform: translateY(-50%) translateX(0);
            }
            50% {
                transform: translateY(-50%) translateX(5px);
            }
        }
    }
}

@media (min-width: 768px) and (max-width: 1023px) {
    .principles {
        .carousel-row {
            height: 80%;
            .bloc {
                justify-content: space-evenly;
            }
        }
    }
}

@media (min-width: 1024px) {
    .principles {
        width: 100%;
        background: #faf9f6;
        height: 80vh;
        h2 {
            text-align: right;
            margin: 2% 0 0 0;
            width: 95%;
        }
        .carousel-row {
            margin-top: 5%;
            height: 70%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-around;
            .bloc-twist {
                display: flex;
                width: 100%;
                justify-content: space-around;
                height: 45%;
                .one,
                .two {
                    height: 100%;
                }
            }
            .bloc {
                background: #ffff;
                border-radius: 10px;
                display: flex;
                width: 20%;
                justify-content: space-around;
                flex-direction: column;
                align-items: center;
                box-shadow: 0 4px 16px rgba(0, 0, 0, 0.04); // lÃ©ger et constant

                h3 {
                    font-size: 2em;
                    text-align: center;
                    color: #c88a16;
                    font-family: "Cormorant-Regular";
                    font-weight: 800;
                }
                p {
                    text-align: center;
                    font-family: "Inter-Regular";
                    font-style: italic;
                    color: #5a5a5a;
                    font-size: 1.3em;
                    width: 97%;
                }
            }
            .three {
                height: 45%;
            }
        }
        .scroll-arrow {
            display: none;
        }
    }
}
