@media (max-width: 767px) {
    .contact {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;

        h2 {
            font-size: 2em;
        }
        .picture {
            width: 100%;
            img {
                width: 100%;
                object-fit: cover;
                height: 100%;
            }
        }
        .hook {
            font-family: "Cormorant-Regular";
            color: #c88a16;
            padding: 7% 0;

            width: 70%;
            p {
                font-size: 1.8em;
                text-align: center;
                font-weight: 900;
                margin: 0;
            }
        }
        .information-contact {
            text-align: center;

            padding: 2rem 1rem;
            border-radius: 15px;
            font-family: "Inter", sans-serif;

            p {
                font-size: 1rem;
                color: #333;
                margin-bottom: 2rem;
                line-height: 1.6;
            }

            .information-personnel {
                display: flex;
                flex-direction: column;
                align-items: center;
                gap: 0.4rem;

                h4 {
                    font-size: 1.1rem;
                    font-weight: 700;
                    color: #000;
                    margin-bottom: 0.2rem;
                }

                h5 {
                    font-size: 1rem;
                    font-weight: 600;
                    color: #c88a16;
                    margin-bottom: 1rem;
                }

                .phone,
                .email {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    gap: 0.5rem;
                    margin-bottom: 0.5rem;

                    svg {
                        color: #c88a16;
                        width: 16px;
                        height: 16px;
                    }

                    a {
                        color: #000;
                        font-size: 0.95rem;
                        text-decoration: none;

                        &:hover {
                            color: #b97b14;
                            transition: color 0.2s ease-in-out;
                        }
                    }
                }
            }
        }
    }
}
@media (min-width: 768px) and (max-width: 1023px) {
    .contact {
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-end;
        width: 100%;
        padding: 7% 0;

        align-content: center;
        h2 {
            order: 1;
            width: 100%;
            text-align: center;
            height: 7%;
            font-size: 2.5em;
        }
        .picture {
            transform: translateY(-25%);
            order: 4;
            width: 50%;

            margin-right: 5%;

            img {
                border-radius: 10px;
                height: 100%;
                width: 100%;
                object-fit: cover;
            }
        }
        .hook {
            order: 2;
            width: 100%;
            font-family: "Cormorant-Regular";
            color: #c88a16;
            font-size: 2em;
            font-weight: 700;

            p {
                font-weight: 700;
                margin-left: 8%;
                font-size: 2em;
                margin-bottom: 0;
                line-height: 1.3;
            }
        }
        .information-contact {
            order: 3;
            p {
                width: 80%;
                margin-left: 8%;
                margin-top: 3%;
                font-size: 1.5em;
                font-family: "Inter-Regular";
            }
            .information-personnel {
                margin-top: 10%;
                margin-left: 8%;
                width: 50%;
                h4 {
                    font-family: "Cormorant-Regular";
                    font-size: 2em;
                    color: #1c1c1c;
                }
                h5 {
                    font-size: 1.5em;
                    color: #c88a16;
                    font-weight: 400;
                }
                .phone,
                .email {
                    display: flex;
                    align-items: center;
                    font-size: 1.4em;
                    svg {
                        width: 1.5em;
                        height: 1.3em;
                        color: #1c1c1c;
                    }
                }
            }
        }
    }
}
@media (min-width: 1024px) {
    .contact {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        align-content: space-between;
        align-items: flex-start;
        height: 140vh;
        .picture {
            position: absolute;
            top: 22%;
            right: 5%;
            width: 30%;
            height: 60%;
            order: 3;
            border-radius: 10px;
            img {
                border-radius: 10px;
                height: 100%;
            }
        }
        .hook {
            margin-top: 5%;
            width: 90%;
            order: 1;
            color: #c88a16;
            font-family: "Cormorant-Regular";
            font-size: 2.3em;
            p {
                margin: 0;
            }
        }
        .form {
            order: 4;
        }
        .information-contact {
            position: absolute;
            left: 5%;
            top: 45%;
            order: 2;
            width: 40%;
            p {
                width: 50%;
                text-align: center;
                font-size: 1.3em;
                font-family: "Inter-Regular";
            }
            .information-personnel {
                h4 {
                    width: 40%;
                    text-align: center;
                    font-family: "Cormorant-Regular";
                    font-size: 2em;
                }
                h5 {
                    color: #c88a16;
                    width: 40%;
                    text-align: center;
                    font-family: "Cormorant-Regular";
                    font-size: 1.5em;
                }
                div {
                    margin-top: 2.5%;
                    display: flex;
                    justify-content: space-between;
                    width: 27%;
                    height: 5vh;
                    align-items: center;
                    font-size: clamp(1em, 1.1vw, 2em);
                    margin-left: 5%;
                }
                .phone {
                    width: 25%;
                }
            }
        }
        .text-center {
            display: flex;
            flex-direction: column;
            align-items: center;
            order: 5;

            background: #faf9f6;
            border-radius: 10px;
            margin-bottom: 150px;
            text-align: center;
            padding: 5% 2%;
            a {
                color: #ffff;
                width: 35%;
                transition: all 0.8s ease-in;
                &:hover {
                    transform: scale(1.2);
                    opacity: 0.7;
                }
            }
            .back {
                padding: 5% 0;
                text-align: center;
                background: #c88a16;
                width: 100%;
                border-radius: 10px;
            }
        }
    }
}
